.Pre {
  text-align: left;
}
.View {
  border: 2px solid orange;
}
.Navi {
  border: 2px solid green;
}
.Root {
  border: 2px solid red;
}
.Main {
  border: 2px solid blue;
}
.Progress {
  position: absolute; right: 20px; z-index: 2;
  border: 1px solid red;
  background: rgba(255,255,255,0.8);
  height: 30px;
  width: 30%;
  box-sizing: border-box;
  padding: 10px;
  .Bar {
    box-sizing: border-box;
    position: absolute; z-index: -1;
    top: 0; left: 0; bottom: 0;
    border-top: 10px solid red;
    background: rgba(255,0,0,0.5);
    text-align: right;
    padding: 0 10px;
  }
}
.Indicator {
  display: flex;
  position: fixed;
  border: 2px solid orange;
  border-radius: 50%;
  background:rgba(255,255,255,.8);
  width: 100px; height: 100px;
  transform: translateX(-50%) translateY(-50%);
  justify-content: center;
  align-items: center;
  transition: border-color .5s;
  &:hover {
    border-color: blue;
  }
}
.List {
  .Item {
    border: 2px solid yellow;
    &__Current {
      border-color: orange;
    }
  }
}


.Flip {
  position: absolute; right: 10%; bottom: 10%;
  width: 30%;
  height: 30%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 8px;

&_Front,
&_Back {
  position: absolute;
  max-width: 500px;
  max-height: 500px;
  width: 50ch;
  height: 50ch;
  cursor: pointer;
  will-change: transform, opacity;

  background-size: cover;
}

&_Back {
  background-image: url(https://images.unsplash.com/photo-1544511916-0148ccdeb877?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&w=1901&q=80i&auto=format&fit=crop);
}

&_Front {
  background-image: url(https://images.unsplash.com/photo-1540206395-68808572332f?ixlib=rb-1.2.1&w=1181&q=80&auto=format&fit=crop);
}
}
.Box {
  position: absolute; z-index: -1; top: 0; left: 0; right: 0; bottom:0;
  opacity:.5;
    will-change: width, height, left, top;
}

.Logo {
  &::before {
    content: " ";
    position: absolute;
    top: 10%;
    left: 15%;
    width: 120%;
    height: 120%;
    // bottom: 0;
    // right: 0;
    background-image: url(data:image/svg+xml;base64,);
    background-repeat: no-repeat;
    // background-position: 300% 300%;
    background-size: 100% 100%;
    opacity: 0.05;
  }

  background-color: #282c34;
  color: #ffffff;

  width: 400px;
  height: 400px;
  margin-left: auto;
  margin-right: auto;
  position: relative;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  h1 {
    font-size: 80px;
    color: #61dafb;
    text-align: center;
    margin: 0;
    letter-spacing: 0.01em;
  }
  p {
    font-size: 75px;
    text-align: center;
    letter-spacing: 0.01em;
    font-weight: 200;
    margin: 0;
  }

}